<template>
	<div class="overview-header">
		<div class="page-tab">
			<ul class="navs leftnavs">
				<li v-for="nav in navList.slice(0, navSplitNum)" :key="nav.name" class="nav leftnav" :class="{selected: nav.selected}" @click="switchPage({to: {name: nav.name}})">
					<span>{{ nav.text }}</span>
				</li>
			</ul>
			<div class="placeholder"></div>
			<ul class="navs rightnavs">
				<li v-for="nav in navList.slice(navSplitNum, 1000)" :key="nav.name" class="nav rightnav" :class="{selected: nav.selected}" @click="switchPage({to: {name: nav.name}})">
					<span>{{ nav.text }}</span>
				</li>
			</ul>
		</div>
		<div class="logo head-logo">
			<p class="logo-title" v-if="!logoUrl">
				<span @click="logoClickfn" :class="{'logo-href': canShowLogoHref()}">{{ titlename }}</span>
			</p>
			<div v-else class="img-logo-box">
				<img :src="logoUrl" :alt="titlename" class="head-logo-pic" />
			</div>
			<a class="head-logo-label" v-if="showLogo">
				<template v-if="!poweredBy"> <span>Powered by</span> 左岸芯慧 </template>
				<span v-else>{{ poweredBy }}</span>
			</a>
		</div>
		<div class="header-user">
			<UserCenter @doLoginOut="doLoginOut"></UserCenter>
		</div>
	</div>
</template>

<script>
import UserCenter from '@/components/common/user-center.vue';
import {getStorage} from '@/utils/storage';
import asyncRouter from '@/router/asyncRouter.js';

export default {
	data() {
		return {
			navList: [],
			titlename: '',
			logoUrl: '',
			showLogo: false,
			poweredBy: '',
		};
	},
	computed: {
		navSplitNum() {
			return Math.ceil(this.navList.length / 2) || 0;
		},
	},
	created() {
		this.checkLogin();
		this.initNavListfn();
		this.refreshPage();
	},
	methods: {
		initNavListfn() {
			var navs = [];
			let someRouterMap = JSON.parse(JSON.stringify(asyncRouter[0]['children']));
			someRouterMap.forEach(item => {
				if (item['headerTitle']) {
					navs.push({
						id: item['key'],
						name: item['name'],
						selected: false,
						text: item['headerTitle'],
					});
				}
			});
			const flag = getStorage('govGovernmentSubjectVO').showMenuType;
			this.navList = navs.filter(nav => {
				return (flag & nav.id) === nav.id;
			});
			if (this.navList.length === 1 && this.navList[0]['name'] === 'overview') {
				this.navList.length = 0;
			}
		},
		// 切换页面效果
		switchPage({to} = {}) {
			if (to.name === 'animalHusbandryManage') return;
			this.$router.push(to);
			this.navList.forEach(item => {
				item.selected = to.name === item.name;
			});
		},
		// 刷新选中不跳
		refreshPage() {
			let routerName = this.$route.name;
			var twoMenuToMain = {
				//二级页面 映射到 一级页面
				plotlist: 'landSource',
			};
			routerName = twoMenuToMain[routerName] || routerName;
			this.navList.forEach(item => {
				item.selected = routerName === item.name;
			});
		},
		checkLogin() {
			if (!getStorage('accessToken')) {
				return true;
			}
			const userInfo = getStorage('govGovernmentUserVO');
			this.getShowLogo(userInfo.governmentSubjectId);
		},
		doLoginOut() {
			setTimeout(() => {
				this.checkLogin();
			}, 500);
		},
		// show logo
		getShowLogo(id) {
			this.$ajax
				.post(this.$api.login.showLogo, {
					id: id,
				})
				.then(({data: res}) => {
					const obj = res.obj;
					if (res.respCode === 0) {
						this.showLogo = obj.showLogoFlag;
						this.logoUrl = obj.subjectLogoUrl;
						this.titlename = getStorage('govGovernmentSubjectVO').subjectName; // 获取区域名称
						this.poweredBy = getStorage('govGovernmentSubjectVO').poweredBy;
						// this.isShowFlag = true;
					}
				});
		},
		logoClickfn() {
			if (!this.canShowLogoHref()) return;
			// window.location.href = 'http://10.121.226.94:81/ncny_fsj';
			window.location.href = 'http://10.121.226.68/ypt';
		},
		canShowLogoHref() {
			/*
			 头部点击后跳转链接的主体id
			 测试服：103 （上海）
			 正式服：49（闵行）、57（嘉定）、74（上海）
			 */
			var canshow = false;
			var subjectvo = getStorage('govGovernmentSubjectVO');
			if (window.location.href.indexOf('govtest') !== -1) {
				if ([103].includes(subjectvo.id)) {
					canshow = true;
				}
			}
			//正式服
			if (window.location.href.indexOf('gov.snkoudai') !== -1) {
				if ([49, 57, 74].includes(subjectvo.id)) {
					canshow = true;
				}
			}
			if (window.location.href.indexOf('192.168') !== -1 || window.location.href.indexOf('127.0') !== -1) {
				canshow = true;
			}
			return canshow;
		},
	},
	components: {
		UserCenter,
	},
};
</script>

<style lang="scss" scoped>
.overview-header {
	position: relative;
	width: 100%;
	height: 115px;
	padding: 0;
	text-align: center;
	background: url('../assets/images/common/header-bg.png') no-repeat top center;
	background-size: auto 115px;
	box-sizing: border-box;
	.logo {
		width: 680px;
		height: 74px;
		position: relative;
		margin: 0 auto 0;
		.img-logo-box {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 70px;
		}
		.head-logo-pic {
			max-width: 580px;
			max-height: 46px;
		}
		p.logo-title {
			width: 100%;
			height: 54px;
			line-height: 50px;
			span {
				font-size: 30px;
				font-family: Microsoft YaHei;
				font-weight: bold;
				color: #ffffff;
				text-shadow: 0 6px 12px rgba(0, 12, 37, 0.82), 0 3px 0 #004c4e, 0 5px 0 #003435, 0 6px 16px rgba(0, 220, 228, 0.58), 1px -3px 12px rgb(0, 224, 236);
				&.logo-href {
					cursor: pointer;
				}
			}
		}
		.head-logo-label {
			font-size: 16px;
			font-family: Microsoft YaHei;
			font-weight: 400;
			color: #00f6ff;
			display: inline-block;
			margin-top: -2px;
		}
	}

	.page-tab {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		display: flex;
		justify-content: center;
		overflow: hidden;
		.placeholder {
			display: inline-block;
			width: 682px;
		}
		.navs {
			width: calc(50% - 341px);
			display: flex;
			justify-content: flex-end;
			&.rightnavs {
				justify-content: start;
			}
			.nav {
				height: 55px;
				line-height: 56px;
				margin: 0 -10px;
				box-sizing: border-box;
				font-size: 16px;
				font-weight: bold;
				cursor: pointer;
				background-size: 100%;
				background-repeat: no-repeat;
				background-position: center;
				background-size: 100% 80%;
				opacity: 0.9;
				span {
					width: 172px;
					color: #00f6ff;
					display: inline-block;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					transition: all 0.2s;
					background-size: 0;
				}
				&.selected {
					opacity: 1;
					line-height: 55px;
					span {
						color: #00f6ff;
						font-size: 18px;
						font-weight: bold;
						text-shadow: 0 0px 25px #001825, 0 2px 0 #006f73, 0 4px 0 #003538;
					}
				}
				&.leftnav {
					background-image: url('../assets/images/common/btn-a-2.png');
				}
				&.rightnav {
					background-image: url('../assets/images/common/btn-a-3.png');
				}
				&.leftnav.selected,
				&.leftnav span {
					background-image: url('../assets/images/common/btn-a-4.png');
				}
				&.rightnav.selected,
				&.rightnav span {
					background-image: url('../assets/images/common/btn-a-1.png');
				}
			}
		}
	}
	.header-user {
		width: 44px;
		height: 55px;
		position: absolute;
		right: 10px;
		top: 0;
	}
}
@media screen and (max-width: 1680px) {
	.overview-header .logo .img-logo-box {
		height: 55px;
	}
	.page-tab .placeholder {
		width: 580px;
	}
	.overview-header .logo .head-logo-label {
		margin-top: -4px;
	}
}
@media screen and (min-width: 1900px) {
	.overview-header {
		height: 100px;
		background-size: auto 130px;
	}
	.overview-header .page-tab .placeholder {
		width: 940px;
	}

	.overview-header .logo p.logo-title {
		width: 100%;
		height: 65px;
		line-height: 60px;
	}
	.overview-header .logo .head-logo-label {
		margin-top: 0px;
	}
	.overview-header .logo p.logo-title span {
		font-size: 38px;
	}
	.overview-header .header-user {
		height: 67px;
		right: 20px;
	}
	.login-container .login-name .login-user-img {
		width: 50px;
		height: 50px;
	}
	.login-container .badge {
		top: 8px;
		right: 2px;
	}
	.overview-header .page-tab .navs {
		height: 70px;
	}
	.overview-header .page-tab .navs .nav {
		line-height: 64px;
		height: 64px;
		margin-top: 4px;
	}
	.overview-header .page-tab .navs .nav span {
		font-size: 18px;
	}
	.overview-header .page-tab .navs .nav.selected span {
		font-size: 20px;
	}
	.overview-header .page-tab .navs .nav.selected {
		line-height: 64px;
	}
}
@media screen and (min-width: 3000px) {
	.overview-header {
		height: 90px;
		background-size: auto 120px;
	}
	.overview-header .page-tab .navs {
		height: 66px;
	}
	.overview-header .page-tab .navs .nav {
		width: 310px;
		height: 65px;
		line-height: 66px;
	}
	.overview-header .page-tab .navs .nav.selected {
		line-height: 66px;
	}
	.overview-header .page-tab .navs .nav span {
		font-size: 24px;
	}
	.overview-header .page-tab .navs .nav.selected span {
		font-size: 26px;
	}
	.overview-header .logo {
		width: 1000px;
	}
	.overview-header .logo .logo-title {
		font-size: 43px;
		line-height: 49px;
	}
	.overview-header .logo .head-logo-label {
		margin-top: -3px;
	}
	.overview-header .page-tab .placeholder {
		width: 1000px;
	}
	.overview-header .header-user {
		height: 67px;
		right: 20px;
	}
	.login-container .login-name .login-user-img {
		width: 50px;
		height: 50px;
	}
}
</style>



